<template>
    <div class="register">
        <div class="head">
            <h1 class="title">
                <span class="icon-chevron-left goBack" @click="back"></span>
                <span class="text">注册</span>
            </h1>
        </div>
        <div class="content">
             <ul class="userInfo">
                <li >
                    <div class="counter_text">
                        <span class="icon-user" ></span>
                        <span>手机号码</span>
                    </div>
                    <div class="couter">
                        <input type="text" placeholder="请输入手机号码" class="phoneNumber" v-model="userPhoneNumber">
                        <span class="icon-ok okSpan" v-if="phoneState"></span>
                    </div>
                </li>
                 <li>
                     <div class="counter_text">
                         <span class="icon-mobile-phone" ></span>
                         <span>验证码</span>
                     </div>
                     <div class="couter intCode">
                         <input type="text" placeholder="请输入验证码" class="verificationCode" >
                     </div>
                     <div class="getCode">
                         <input type="button" :value="confrimCodeText" @click="confirmCode" :class="{codeRequest:codeState}" :disabled="codeState">
                     </div>
                 </li>
                <li>
                    <div class="counter_text">
                        <span class="icon-wrench" ></span>
                        <span>密码</span>
                    </div>
                    <div class="couter">
                        <input type="password" placeholder="6-12位以字母数字组成" class="passWord" v-model="userPassWard">
                        <span class="icon-ok okSpan" v-if="passWardState"></span>
                    </div>
                </li>
                 <li>
                     <div class="counter_text">
                         <span class="icon-wrench" ></span>
                         <span>确认密码</span>
                     </div>
                     <div class="couter">
                         <input type="password" placeholder="请再次输入密码" class="passWord" v-model="confirmPassWard">
                         <span class="icon-ok okSpan" v-if="passWardConfirm"></span>
                     </div>
                 </li>
                    
            </ul>
            <p class="clause">
                <input type="checkbox" @click="checkState=!checkState">
                注册后美美将按照《**********》条款为您服务
            </p>
            <p class="register_p">
                <input type="button" value="注册" class="registerBtn" @click="registerConfirm">
            </p>
        </div>
        <v-triangle></v-triangle>
        <div class="registerOk" v-if="registerOk">
            <p>注册成功，马上转到登录！</p>
        </div>
    </div>
</template>
<script>
    import triangle from "../login/triangle.vue"
    import oTools from "../../public/js/tools"
    export default {
        data() {
            return {
                confrimCodeText:"获取验证码",
                codeState:false,
                codeRequestTime:60,
                codeTime:null,
                userPhoneNumber:null,
                phoneState:false,
                userPassWard:null,
                confirmPassWard:null,
                passWardState:false,
                passWardConfirm:false,
                checkState:false,
                modelState:false,
                modelText:null,
                userQuantity:null,
                registerOk:false
            }
        },
        watch:{
            userPhoneNumber:function(){
                this.test();
            },
            userPassWard(){
                this.test();
            },
            confirmPassWard(){
                this.test();
            },
            codeRequestTime(){
                if(this.codeRequestTime>=60){
                    this.confrimCodeText="重新获取";
                }else{
                    this.confrimCodeText=this.codeRequestTime+"s后重新获取";
                }
            }
        },
        mounted(){
            if(localStorage.locationUser){
                this.userQuantity = localStorage.locationUser.length;
            }else{
                this.userQuantity = 0;
            }
        },
        methods:{
            back(){
                this.$router.go(-1)
            },
            test(){
                let phoneReg=/^1[34578]\d{9}$/;
                if(phoneReg.test(this.userPhoneNumber)){
                    this.phoneState=true;
                }else{
                    this.phoneState=false;
                }
                let passWardReg=/^[a-zA-Z0-9]{6,12}$/;
                if(passWardReg.test(this.userPassWard)){
                    this.passWardState=true;
                }else{
                    this.passWardState=false;
                }
                if(this.confirmPassWard && this.userPassWard === this.confirmPassWard){
                    this.passWardConfirm=true;
                }else{
                    this.passWardConfirm=false;
                }
            },
            confirmCode(){
                this.codeState=true;
                let self=this;
                this.codeTime=setInterval(function(){
                    self.codeRequestTime -=1;
                    if(!self.codeRequestTime){
                        clearInterval(self.codeTime);
                        self.codeState=false;
                        self.codeRequestTime = 60;
                    }
                },100)
            },
            registerConfirm(){
                if(this.phoneState){
                    if(this.passWardState){
                            if(this.passWardConfirm){
                                if(this.checkState){
                                    this.register();
                                }else{
                                    alert("请阅读《***》条款，确认无误后，请选择同意");
                                }
                            }else{
                                alert("两次输入的密码不一致，请重新输入");
                                this.confirmPassWard=null;
                            }
                    }else{
                        alert("请输入正确的密码");
                        this.userPassWard=null;
                    }
                }else{
                    alert("请输入正确的手机号");
                    this.userPhoneNumber=null;
                }
            },
            register(){
                let userNumber=this.userPhoneNumber;
                let userExist=oTools.locationGetUser(userNumber,"locationUser");
                if(userExist){
                    alert("用户已经注册，请登录");
                }else{
                    this.userQuantity++;
                    if(this.userQuantity<10){
                        this.userQuantity="0"+this.userQuantity;
                    }
                    let user={};
                    user.userId=this.userQuantity;
                    user.phoneNumber = this.userPhoneNumber;
                    user.passWard=this.userPassWard;
//                **************加入到location中***********
                    oTools.locationPushUser(user,"locationUser");
//                *******跳转到登录页面******
                    this.registerOk=true;
                    let self=this;
                    setTimeout(function(){
                        self.$router.replace("/login");
                    },1000);
                }
            },
            getUser(userName){
                let userData=null;
                let storage = window.localStorage.getItem('locationUser');
                storage=eval(storage);
                for(let i = 0 ; i < storage.length ; i++){
                    if(userName == storage[i].phoneNumber){
                         userData = storage[i];
                        return userData;
                    }
                }
            }
        },
        components:{
            "v-triangle":triangle
        }
    }
</script>

<style lang="less" scoped>
    @import "../../public/less/base.less";
    .head {
        padding-top: 0.5rem;
        background: #2bc57c;
        margin-bottom: 1rem;
    }

    .title {
        display: flex;
        height: 1.8rem;
        padding: 0 .5rem;
        justify-content: space-between;
        align-items: center;
        color: white;
        font-size: 1rem;
        line-height: 1.8rem;
    .goBack {
        width: 2rem;
        text-align: center;
        font-weight: 100;
    }
    .text{
        flex: 1;
        padding-right: 2rem;
        text-align: center;
    }
    a {
        width: 2rem;
        color: white;
        font-size: .8rem;
    }
    }
    
    .content {
        .userInfo {
            padding: 0 1rem;
            li {
                display: flex;
                justify-content:space-between;
                padding-top: .5rem;
                height: 2rem;
                line-height: 2rem;
                .border1px(1px, rgba(100, 100, 100, .5));
                .okSpan{
                    color: #2bc57f;
                }

                .counter_text {
                    display: flex;
                    justify-content:space-between;
                    width: 6rem;
                    height: 2rem;
                    font-size: .75rem;
                    text-align: center;

                    span:nth-child(1) {
                            width: 2rem;
                            padding-top: .6rem;
                            text-align: center;

                        }
                    span:nth-child(2) {
                            width:4rem;
                        margin-left: -.6rem;
                             padding: 0 .2rem;
                            text-align-last: justify;

                         }
                }
                .couter {
                    flex: 1;

                    input {
                        line-height: 1.5rem;
                        border: none;
                        padding: .2rem.5rem;
                        font-size: .75rem;
                        &::-webkit-input-placeholder,
                        textarea::-webkit-input-placeholder {
                            color: rgba(50, 50, 50, .3);
                        }
                        &:-ms-input-placeholder,
                        textarea:-ms-input-placeholder {
                            color: rgba(50, 50, 50, .3);
                        }
                        &:-moz-placeholder,
                        textarea:-moz-placeholder {
                            color: rgba(50, 50, 50, .3);
                        }
                    }
                    &.intCode{
                        width: 5rem;
                    }
                }
                .getCode{
                    width: 4.5rem;
                    padding:0;
                    input{
                        width: 100%;
                        height: 70%;
                        line-height: 100%;
                        border: none;
                        color:white;
                        background-color: #2dc57c;
                        font-size: .6rem;
                        border-radius: 1rem;
                        &.codeRequest{
                            background-color: rgba(100,100,100,.5);
                        }
                    }
                }

            }
        }
    .clause{
        padding:.5rem 1rem;
        color: rgba(50, 50, 50, .6);

    }
    .register_p{
        height: 2rem;
        margin-top: 2rem;
        padding: 0 2rem;
        line-height: 100%;
        input{
            width: 100%;
            height: 100%;
            line-height: 100%;
            color: white;
            border:none;
            border-radius: 2rem;
            background-color: #2bc57c;
            font-size: .8rem;
        }
    }
    }
    .registerOk{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        line-height: 100vh;
        text-align: center;
        color: #2bc57f;
        font-size: 1rem;
        font-weight: 100;
        background-color: rgba(100,100,100,.8);
    }
</style>


