<template>
    <div id="recommendation">
        <v-rec-header :title="oTitle"></v-rec-header>
        <v-banner :bannerData="recData.banner"></v-banner>
        <v-sideMenu :sideMenu="recData.sideMenu"></v-sideMenu>
        <v-hotSell :hotGoods="recData.hotSell"></v-hotSell>
        <v-listItem :indexListData="indexList"></v-listItem>
    </div>
</template>

<script>
    const err_ok = 0;
    import oRecHeader from './header/header.vue'
    import oBanner from './../banner/banner.vue'
    import oSideMenu from '../indexSideMenu/indexSideMenu.vue'
    import oHotSell from '../hotSell/hotSell.vue'
    import oListItem from '../indexList/indexList.vue'
    export default {
        data() {
            return {
                recData: {},
                oTitle:"精选",
                indexData:{},
                indexList:[]
            }
        },
        beforeMount() {
            this.$nextTick(function(){
                this.$http.get("./api/recommendation").then((res) => {
                let data = res.data;
                if (data.errno === err_ok) {
                    this.recData = data.data;
                }
                 })

                this.$http.get("./api/index").then((response) => {
                    let indexData = response.data;
                if (indexData.errno === err_ok) {
                    this.indexData = indexData.data;
                    this.indexList=this.indexData.indexListData.slice(0,10);
                }
            })
            })
        },
        components: {
            "v-rec-header": oRecHeader,
            "v-banner": oBanner,
            "v-sideMenu": oSideMenu,
            "v-hotSell":oHotSell,
            "v-listItem":oListItem
        }
    }
</script>

<style scoped lang="less">
    #recommendation{
        padding-bottom: 2.2rem;
    }
</style>


