<template>
  <div class="home"
       @touchstart="reLoadStart"
       @touchmove="reLoadMove"
       @touchend="reLoadEnd"
  >
        <div class="index" v-if="currentIndex==0" key="currentIndex">
            <v-header></v-header>
            <transition name="bounce">
                <div v-show="reLoadState" v-text="reLoadText" class="refresh"></div>
            </transition>
            <v-banner :bannerData="dataAll.banner"></v-banner>
            <v-sideMenu :sideMenu="dataAll.sideMenu1"></v-sideMenu>
            <v-sideMenu :sideMenu="dataAll.sideMenu2"></v-sideMenu>
            <v-scrollNews :newsData="dataAll.news"></v-scrollNews>
            <v-sideBanner></v-sideBanner>
            <v-indexList :indexListData="indexList"></v-indexList>
            <transition name="bounce">
                <div v-show="refreshState" class="refresh" v-text="refreshText">
                    拼命加载中。。。
                </div>
            </transition>
        </div>
  </div>
</template>

<script>
    import oBanner from '../banner/banner.vue'
    import oHeader from '../header/header.vue'
    import oFooter from '../footer/footer.vue'
    import oSideMenu from '../indexSideMenu/indexSideMenu.vue'
    import oScrollNews from '../scrollNews/scrollNews.vue'
    import oSideBanner from '../indexSideBanner/indexSideBanner.vue'
    import oIndexList from '../indexList/indexList.vue'
    import oTools from '../../public/js/tools.js'
    const err_ok = 0;

    export default {
        data() {
            return {
                currentIndex: 0,
                dataAll: {},
                indexList:[],
                indexListLength:5,
                refreshState : false,
                reLoadState : false,
                timer1:null,
                refreshText:"拼命加载中。。。",
                reLoadText:"拼命刷新中。。。",
                startY:null
            }
        },
        created() {
            this.reload();
        },
        components: {
            "v-banner": oBanner,
            "v-header": oHeader,
            "v-sideMenu": oSideMenu,
            "v-sideBanner": oSideBanner,
            "v-scrollNews": oScrollNews,
            "v-indexList": oIndexList,
            "v-footer": oFooter
        },
        mounted(){
            let self=this;
            window.onscroll = function(){
                oTools.scrollUp(self,self.refreshState);
            }
        },
        methods:{
            addList(){
                let self=this;
                this.indexListLength +=5;
                if(this.indexListLength>this.dataAll.indexListData.length){
                    this.refreshText="加载完毕哦";
                    setTimeout(function(){
                        self.refreshState = false;
                    },300);
                }else{
                    this.refreshText="已更新！";
                    setTimeout(function(){
                        self.refreshState = false;
                        self.refreshText="拼命加载中。。。";
                        self.indexList=self.dataAll.indexListData.slice(0,self.indexListLength);
                    },300)
                }
            },
            reLoadStart(){
                let touch = event.changedTouches[0];
                this.startY=touch.clientY;
            },
            reLoadMove(){
                let scrollTop = document.body.scrollTop;
                let touch = event.changedTouches[0];
                let moveY = touch.clientY;
                let distance = parseInt(this.startY - moveY);
                if(distance<-20 && scrollTop==0){
                    this.reLoadText="拼命刷新中。。。。";
                    this.reLoadState=true;
                }
            },
            reLoadEnd(){
                if(this.reLoadState){
                    this.reload();
                    let self=this;
                    setTimeout(function(){
                        self.reLoadText="刷新完成！";
                        setTimeout(function(){
                            self.reLoadState=false;
                        },300);
                    },800)
                }
            },
            reload(){
                this.$http.get("./api/index").then((res) => {
                    let data = res.data;
                if (data.errno === err_ok) {
                    this.dataAll = data.data;
                    this.indexList=this.dataAll.indexListData.slice(0,this.indexListLength);
                }
            })
            }
        }
    }
</script>

<style lang="less" scoped>
    .home {
        position: relative;
        height: 100vh;
    }
    
    .index {
        padding-bottom: 2.2rem;
    }
    .refresh{
        width: 100%;
        overflow: hidden;
        font-size: .7rem;
        text-align: center;
    }
    .bounce-enter{
        transform: translateX(20rem);
    }
    .bounce-leave-to{
        transform: translateX(-20rem);
    }
    .bounce-enter-active,
    .bounce-leave-active{
        transition: all .2s;
    }
</style>


