<template>
  <div class="star">
      <span v-for="item in itemClasses" :class="'star-' + item" class="star-item"></span>
  </div>
</template>
<script>
    // 定义星星的数量，通过分数来控制result数组中的字符串，
    // 给对应的字符拼接好类名写好样式，通过控制数组的变化来控制最总的样式
    const starLength = 5;
    const starOn = "on";
    const starHalf = "half";
    const starOff = "off";

    export default {
        data() {
            return {

            }
        },
        props: {
            score: {
                type: Number
            }
        },
        computed: {
            itemClasses() {
                let result = [];
                let score = Math.floor(this.score * 2) / 2; //分数0.5取整，0.1-0.5就舍弃，0.5-0.9就加上
                let halfHas = score % 1 !== 0; //判断有无半星
                let integer = Math.floor(score); //全星的数量
                for (let i = 0; i < integer; i++) {
                    result.push(starOn);
                }
                if (halfHas) {
                    result.push(starHalf); //有半星就加上
                }
                while (result.length < starLength) {
                    result.push(starOff); //最后补上空白的星星
                }
                return result;
            }
        }
    };
</script>

<style lang="less">
    .star {
        display: flex;
        justify-content: center;
        .star-item {
            flex: 1;
            height:1.2rem;
            background-repeat: no-repeat;
            background-size: content;
            /*数组中的字符串拼接成以下的类名，就可以实现数组控制样式*/
            &.star-on {
                background-image: url('./star24_on@2x.png');
            }
            &.star-half {
                background-image: url('./star24_half@2x.png');
            }
            &.star-off {
                background-image: url('./star24_off@2x.png');
            }
        }
    }
</style>


