<template>
    <div id="message"
         @touchstart="reLoadStart"
         @touchmove="reLoadMove"
         @touchend="reLoadEnd"
    >

        <v-header :title="oTitle"></v-header>
        <transition name="bounce">
            <div v-show="reLoadState" v-text="reLoadText" style="text-align: center"></div>
        </transition>
        <v-banner :bannerData="dataAll.banner"></v-banner>
        <v-listItem :listData="messageList"></v-listItem>
        <transition name="bounce">
            <div v-show="refreshState" class="refresh" v-text="refreshText">
                拼命加载中。。。
            </div>
        </transition>
    </div>
</template>

<script>
    import oMgeHeader from '../recommendation/header/header.vue'
    import oBanner from '../banner/banner.vue'
    import oListItem from './msgListItem/msgListItem.vue'
    import oTools from '../../public/js/tools.js';
    const err_ok = 0;
    export default {
        data() {
            return {
                dataAll:{},
                oTitle: "圈子",
                messageList:[],
                messageListLength:5,
                refreshState : false,
                reLoadState : false,
                timer1:null,
                refreshText:"拼命加载中。。。",
                reLoadText:"拼命刷新中。。。",
                startY:null
            }
        },
        beforeMount() {
            this.$nextTick(function() {
                this.$http.get("./api/message").then((res) => {
                    let data = res.data;
                    if (data.errno === err_ok) {
                        this.dataAll = data.data;
                        this.addList();
                    }
                })
            })
        },
        components: {
            "v-header": oMgeHeader,
            "v-banner": oBanner,
            "v-listItem": oListItem
        },
        mounted(){
            let self=this;
            window.onscroll = function(){
                oTools.scrollUp(self,self.refreshState);
            }
        },
        methods:{
            addList(){
                let self=this;
                this.messageListLength +=5;
                if(this.messageListLength>this.dataAll.messageList.length){
                    this.refreshText="加载完毕哦";
                    setTimeout(function(){
                        self.refreshState = false;
                    },300);
                }else{
                    this.refreshText="已更新！";
                    setTimeout(function(){
                        self.refreshState = false;
                        self.refreshText="拼命加载中。。。";
                        self.messageList=self.dataAll.messageList.slice(0,self.messageListLength);
                    },300)
                }
            },
            reLoadStart(){
                let touch = event.changedTouches[0];
                this.startY=touch.clientY;
            },
            reLoadMove(){
                let scrollTop = document.body.scrollTop;
                let touch = event.changedTouches[0];
                let moveY = touch.clientY;
                let distance = parseInt(this.startY - moveY);
                if(distance<-20 && scrollTop==0){
                    this.reLoadState=true;
                }
            },
            reLoadEnd(){
                if(this.reLoadState){
                    this.reload();
                    let self=this;
                    setTimeout(function(){
                        self.reLoadText="刷新完成！";
                        setTimeout(function(){
                            self.reLoadState=false;
                            self.reLoadText="拼命刷新中。。。。";
                        },300);
                    },800)
                }
            },
            reload(){
                this.$http.get("./api/message").then((res) => {
                    let data = res.data;
                    if (data.errno === err_ok) {
                        this.dataAll = data.data;
                    }
                })
            }
        }

    }
</script>

<style scoped lang="less">
    #message{
        padding-bottom: 2.2rem;
    }
    .refresh{
        font-size: .7rem;
        text-align: center;
    }
    .bounce-enter,.bounce-leave-to{
        transform: translateY(2.5rem);
    }
    .bounce-enter-active,
    .bounce-leave-active{
        transition: all .3s;
    }
</style>


