<template>
    <div class="head">
        <h1 class="title">
            <span class="icon-th-large menuHeader"></span>
            <span>{{title}}</span>
            <span class="icon-shopping-cart message"></span>
        </h1>
    </div>
</template>

<script>
    export default{
            data(){
                return{}
            },
            props:{
                title:[String,Object]
            }
    }
</script>
<style lang="less" scoped>
    .head {
        padding-top: 0.5rem;
        background: #2bc57c;
    }
    .title {
        display: flex;
        height: 1.8rem;
        justify-content:space-around;
        align-items:center;
        color: white;
        font-size: 1rem;
        line-height: 1.8rem;
        font-weight:100;
        span{
        text-align:center;
            &:nth-child(odd){
                   flex: 1;
               }
            &:nth-child(2){
                 flex:4;
                text-align: center;
             }
        }

    }
</style>


