<template>
 <div>
     <!--:class="{border:sideMenu.borderNeed}"-->
      <div v-if="sideMenu" class="indexSideMenu" :class="{border:sideMenu.borderNeed}">
        <router-link v-for="(item,index) in sideMenu.mainData" :to="{name:'menuDetail',params:{dataId:item.dataId,title:item.text}}" class="sideMenuBar" :key ="index" >
        <img :src="item.img">
        <p>{{item.text}}</p>
        </router-link>
     </div>
 </div>
</template>

<script>
    export default {
        data() {
            return {
                dataAll:{}
            }
        },
        props: {
            sideMenu: [Object,Array]
        }


    }
</script>

<style scoped lang="less">
    @import "../../public/less/base.less";
    .indexSideMenu {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        .sideMenuBar {
            flex: 1;
            padding: .5rem 0;
            color: #666;
            text-align: center;
            img {
                width: 50%;
            }
            p {
                text-align: center;
            }
        }
    }
    
    .border {
        .border1px (1px, #666);
    }
</style>


