<template>
    <div class="menuDetail">
        <!--<div class="head">-->
            <!--<span  @click="back" class="icon-chevron-left goBack"></span>-->
            <!--<h3 class="title"><span class="titleName">{{oTitle}}</span> Zoom</h3>-->
        <!--</div>-->
        <div class="head">
            <h1 class="title">
                <span  @click="back" class="icon-chevron-left goBack"></span>
                <span class="text"><span class="titleName">{{oTitle}}</span> Zoom</span>
                <span class="icon-share"></span>
            </h1>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                oTitle:null
            }
        },
        beforeRouteEnter(to, from, next){
            let dataId = to.params.dataId;
            let title=to.params.title;
            next(function(vm){
                vm.oTitle=title;
            });
        },
        methods: {
            back(){
                this.$router.go(-1)
            }
        }
    }
</script>

<style scoped lang="less">
    .head {
        padding-top: 0.5rem;
        background: #2bc57c;
    }
    .title {
        display: flex;
        height: 1.8rem;
        justify-content:space-around;
        align-items:center;
        color: white;
        font-size: .8rem;
        line-height: 1.8rem;
        font-weight: 100;
    .titleName{
        color: lighten(orange,20%);
    }
    span{
        text-align:center;
    &:nth-child(odd){
         flex: 1;
         font-size:.8rem;

     }
    &:nth-child(2){
         flex:4;
         text-align: center;
     }
    }
    }
</style>


