<template>
    <div>
        <div id="menuTab">
          <div  @click="currentIndex=0" :class="{active:currentIndex==0}">
              <router-link to="/home/index" >
                  <span class="icon-home"></span>
                  <span>主页</span>
              </router-link>
          </div>
          <div  @click="currentIndex=1" :class="{active:currentIndex==1}">
              <router-link to="/home/recomendation">
                  <span class="icon-food"></span>
                  <span>精选</span>
              </router-link>
          </div>
          <div  @click="currentIndex=2" :class="{active:currentIndex==2}">
              <router-link to="/home/message">
                  <span class="icon-comments-alt"></span>
                  <span>圈子</span>
              </router-link>
          </div>
          <div @click="currentIndex=3" :class="{active:currentIndex==3}">
              <router-link to="/home/userCenter">
                  <span class=" icon-user"></span>
                  <span>坊主</span>
              </router-link>
          </div>
      
  </div>
        <router-view></router-view>
    </div>
      
</template>

<script>
    export default {
        data() {
            return {
                currentIndex: 0
            }
        }
    }
</script>

<style lang="less" scoped>
    #menuTab {
        display: flex;
        position: fixed;
        z-index: 100;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2.2rem;
        padding-top: .2rem;
        background: #fff;
        .active a {
            span:nth-child(1) {
                color: #2bc57f;
            }
            span:nth-child(2) {
                letter-spacing: .5rem;
                padding-left: .5rem;
            }
        }
        div {
            flex: 1;
            text-align: center;
            a {
                color: #666;
                text-align: center;
                span {
                    display: block;
                    font-size: .7rem;
                    &:nth-child(1) {
                        font-size: 1.1rem;
                    }
                }
            }
        }
    }
</style>


