<template>
    <div class="triangle" >
        
        <div class="toggle">
            <div class="line"></div>
            <div class="icon" :class="triangleState ? 'icon-chevron-down':'icon-chevron-up'" @click="triangleState=!triangleState"></div>
            <div class="line"></div>
        </div>
      
        <transition name="bounce">
             <div class="content" v-show="triangleState">
                <h3>第三方登录</h3>
                <ul>
                    <li v-for="item in dataTriangle">
                        <img :src="item.icon">
                    </li>
                </ul>
            </div>
        </transition>
       
        
    </div>
</template>
<script>
    export default {
        data() {
            return {
                triangleState: false,
                dataTriangle: [{
                        "dataId": "QQ",
                        "icon": "../../../static/img/triangle/qq.png"
                    }, {
                        "dataId": "zhifubao",
                        "icon": "../../../static/img/triangle/zhifubao.png"
                    }, {
                        "dataId": "sina",
                        "icon": "../../../static/img/triangle/sina.png"
                    }, {
                        "dataId": "taobao",
                        "icon": "../../../static/img/triangle/taobao.png"
                    }, {
                        "dataId": "weixin",
                        "icon": "../../../static/img/triangle/weixin.png"
                    },

                ]
            }
        }
    }
</script>
<style lang="less" scoped>
    .triangle {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        padding-bottom: .5rem;
        transition: .5s all ease;
    }
    
    .toggle {
        display: flex;
        justify-content: space-around;
        height: 2rem;
        padding: 0 1rem;
        line-height: 2rem;
        .icon {
            box-sizing: border-box;
            width: 2rem;
            height: 100%;
            margin: 0 .5rem;
            padding-top: .5rem;
            line-height: 100%;
            font-size: .8rem;
            color: #2bc57c;
            border: 1px solid #2bc57c;
            border-radius: 50%;
            text-align: center;
        }
        .line {
            flex: 1;
            height: 2rem;
            margin-top: 1rem;
            border-top: 1px solid #2bc57c;
        }
    }
    
    .content {
        transition: .5s all;
        h3 {
            margin-top: .5rem;
            line-height: 1.5rem;
            color: rgba(50, 50, 50, .3);
            text-align: center;
        }
        ul {
            display: flex;
            padding: 0 2rem 1rem;
            li {
                flex: 1;
                height: 3rem;
                line-height: 3rem;
                text-align: center;
                img {
                    width: 60%;
                    height: 60%
                }
            }
        }
    }
    /***************************/
    
    .bounce-enter-active {
        animation: bounce-in .5s;
    }
    
    .bounce-leave-active {
        animation: bounce-out .5s;
    }
    
    @keyframes bounce-in {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1.2);
        }
        100% {
            transform: scale(1);
        }
    }
    
    @keyframes bounce-out {
        0% {
            transform: scale(1);
        }
        50% {
            transform: scale(1.2);
        }
        100% {
            transform: scale(0);
        }
    }
</style>


