<template>
    <div class="menuList">
        <ul>
            <li v-for="(item,index) in menuListData" class="menuList_li">
                <router-link :to="item.dataId">
                    <span>
                        <img :src="item.icon">
                    </span>
                    <span>
                        {{item.menuName}}
                    </span>
                    <span class=" icon-chevron-right"></span>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
    export default{
        data(){
            return{

            }
        },
        props:{
            menuListData:[Array]
        }
    }
</script>
<style lang="less" scoped>
    @import "../../../public/less/base.less";
.menuList_li{
    padding:0 1rem;
    a{
        display: flex;
        height:2rem;
        padding:.5rem 0;
    .border1px(1px,rgba(100,100,100,.5));
    span{
        line-height: 2rem;
        text-align: center;
    }
        span:nth-child(odd){
            flex: 1;
            color: #555;
            img{
                width: 70%;
                height: 70%;
            }
        }
        span:nth-child(even){
             flex: 6;
            padding-left: .5rem;
            text-align: left;
            font-size: .8rem;
            color: #555;
         }
    }

}
</style>


