<template>
    <div id="login">
        <div class="head">
            <h1 class="title">
                <span  @click="back" class="icon-chevron-left goBack"></span>
                <span class="text">登录</span>
                <router-link to="/register">
                    注册
                </router-link>
            </h1>
        </div>
        <v-form></v-form>
        <v-triangle></v-triangle>
    </div>
</template>
<script>
    import oForm from "./form.vue"
    import oTriangle from "./triangle.vue"
    export default {
        data() {
            return {

            }
        },
        mounted(){
          console.log(localStorage);
        },
        methods:{
            back(){
                this.$router.go(-1)
            }
        },
        components: {
            "v-form": oForm,
            "v-triangle": oTriangle
        }
    }
</script>

<style lang="less" scoped>
    .head {
        padding-top: 0.5rem;
        background: #2bc57c;
        margin-bottom: 5rem;
    }
    
    .title {
        display: flex;
        height: 1.8rem;
        padding: 0 .5rem;
        justify-content: space-between;
        align-items: center;
        color: white;
        font-size: 1rem;
        line-height: 1.8rem;
        .goBack {
            width: 2rem;
            text-align: center;
            font-weight: 100;
        }
        .text{
            flex: 1;
            text-align: center;
        }
        a {
            width: 2rem;
            color: white;
            font-size: .8rem;
        }
    }
</style>


