<template>
    <div id="userCenter">
        <div class="header">
            <h1 class="title">
                <span></span>
                <span>坊主</span>
                <span >设置</span>
            </h1>
        </div>
        <div class="content">
            <div class="userPic">
                <div class="userBackground">
                    <img :src="userData.userBackground">
                </div>
                <div>
                    <router-link to="/login" class="login">
                        <div class="userBorder"></div>
                        <div class="userPhoto">
                                <img :src="userData.userBackground">
                        </div>
                        <div class="login_text" v-if="userId">
                            <p></p>
                            <p><span v-text="loginState ? '你好，'+userName:'点击登录'"></span></p>
                        </div>
                    </router-link>
                </div>
            </div>
            <v-menuList :menuListData="userData.userMenuList"></v-menuList>
            <div class="logout" v-if="loginState" @click="logoutF">
                    退出登录
            </div>
        </div>
    </div>
</template>

<script>
    const err_ok=0;
    import oMenuList from "./menuList/menuList.vue"
    import oTools from "../../public/js/tools"
    export default {
            data(){
                return{
                    userData:{},
                    userId:1,
                    loginState:false,
                    userName:null
                }
            },
        components:{
            "v-menuList":oMenuList
        },
        beforeMount() {
            this.$nextTick(function(){
                this.$http.get("/api/userCenter").then((res) => {
                    let data = res.data;
                    if (data.errno === err_ok) {
                        this.userData = data.data;
                    }
                })
                let userState=localStorage.getItem("currentUser");
                if(userState){
                    userState=eval(userState);
                    this.userName=userState[0].phoneNumber;
                    console.log(this.userName,userState);
                    this.loginState=true;
                }else{
                    this.loginState=false;
                }
            })
        },
        methods:{
            logoutF(){
                console.log(666);
                window.localStorage.removeItem("currentUser");
                let self=this;
                setTimeout(function(){
                    self.loginState=false;
                },1000)
            }
        }
    }
</script>

<style scoped lang="less">
    @import "../../public/less/base.less";
    #userCenter{
        padding-bottom: 2.2rem;
    }
    .header {
        padding-top: 0.5rem;
        background: #2bc57c;
        .title {
            display: flex;
            height: 1.8rem;
            justify-content:space-around;
            align-items:center;
            color: white;
            font-size: 1rem;
            line-height: 1.8rem;
            font-weight:100;
        span{
            text-align:center;
            &:nth-child(odd){
                 flex: 1;
                 font-size: .8rem;
             }
            &:nth-child(2){
                 flex:4;
                 text-align: center;
             }
        }
        }
    }
    .content{
        .userPic{
            position: relative;
            height: 11rem;
        .userBackground{
            height:9rem;
            overflow: hidden;
            font-size: 0;
            border-bottom:3px solid #2bc57f;
        }
            img{
                width: 100%;
                height: 101%;

            }
            .login{
                display: flex;
                position: absolute;
                z-index:10;
                bottom: 0rem;
                height: 4rem;
                padding-left:2rem;
                .userBorder{
                    position: absolute;
                    z-index:-1;
                    left: 1.9rem;
                    bottom: -.15rem;
                    width: 4.2rem;
                    height: 2.15rem;
                    background-color: #2bc57f;
                    border-radius:0 0 2.1rem 2.1rem;
                }
                .login_text{
                    width: 12rem;
                    line-height: 2rem;
                color:#666;
                font-size:.8rem;
                    p{
                        height: 2rem;
                        padding-left: 1rem;
                    }
                }
            }
            .userPhoto{
                display: block;
                width: 4rem;
                height: 4rem;
                border-radius:50%;
                overflow: hidden;
                img{
                    width: 100%;
                    height: 100%;
                }
            }

        }
    .logout{
        margin:0 1.1rem;
        line-height: 2rem;
        color: #666;
        font-size: .7rem;
        text-align: center;
        border-radius: 2rem;
        border-bottom: 1px solid #2bc57f;
    }
    }

</style>


