<template>
    <div class="indexSideBanner">
       <ul>
            <transition-group name="bounce">
           <li v-for="(item,index) in sideBanner" class="SB_list" 
                 v-if="currentShow==index" 
                @touchstart="onTouchStart"
                @touchend="onTouchEnd"
                :key="index">
               <h2>{{item.title}} <span class="list_text">{{item.text}}</span></h2>
               <div class="details">
                   <router-link v-for="detail in item.details" to="detail.dataId" class="detail_link" :key="'detail'+index">
                       <img :src="detail.img">
                   </router-link>
               </div>
           </li>
           </transition-group>
       </ul>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                currentShow: 0,
                startX: 0,
                autoScroll: null,
                sideBanner: [{
                        title: "秀早餐",
                        text: "一日之计在于晨",
                        details: [{
                            img: "/static/img/indexSideBanner/indexSideBanner01.png",
                            dataId: "01"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner02.png",
                            dataId: "02"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner03.png",
                            dataId: "03"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner04.png",
                            dataId: "04"
                        }]
                    }, {
                        title: "秀午餐",
                        text: "正在给身体充电",
                        details: [{
                            img: "/static/img/indexSideBanner/indexSideBanner01.png",
                            dataId: "02"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner02.png",
                            dataId: "02"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner03.png",
                            dataId: "02"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner04.png",
                            dataId: "01"
                        }]
                    }, {
                        title: "秀晚餐",
                        text: "让夜晚更有味道",
                        details: [{
                            img: "/static/img/indexSideBanner/indexSideBanner01.png",
                            dataId: "03"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner02.png",
                            dataId: "03"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner03.png",
                            dataId: "03"
                        }, {
                            img: "/static/img/indexSideBanner/indexSideBanner04.png",
                            dataId: "04"
                        }]
                    }

                ]
            }
        },
        mounted() {
            this.autoPlay();
        },
        methods: {
            autoPlay() {
                let self = this;
                let len = this.sideBanner.length;
                this.autoScroll = setInterval(function() {
                    self.currentShow++;
                    if (self.currentShow > len - 1) {
                        self.currentShow = 0;
                    }
                }, 4000);
            },
            onTouchStart(event) {
                clearInterval(this.autoScroll);
                let touch = event.changedTouches[0];
                this.startX = touch.clientX;
            },
            onTouchEnd() {
                let touch = event.changedTouches[0];
                let endX = touch.clientX;
                let distance = Math.abs(this.startX - endX);
                let len = this.sideBanner.length;
                //往右
                if (distance > 20) {
                    if (this.startX < endX) {
                        this.currentShow++;
                        if (this.currentShow > len - 1) {
                            this.currentShow = 0;
                        }

                    } else {
                        this.currentShow--;
                        if (this.currentShow < 0) {
                            this.currentShow = len - 1
                        }
                    }
                }
                this.autoPlay();
                console.log(222)
            }
        }

    }
</script>
<style lang="less" scoped>
    .indexSideBanner ul {
        position: relative;
        width: 100%;
        height: 5.5rem;
        overflow: hidden;
    }
    
    .SB_list {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        overflow: hidden;
        text-align: center;
        h2 {
            margin: .4rem 0;
            font-size: .8rem;
            color: black;
        }
        .list_text {
            margin-left: 0.5rem;
            color: #666;
            font-size: .6rem;
        }
        .details {
            display: flex;
            flex-wrap: no-wrap;
            justify-content: space-around;
            width: 100%;
            .detail_link {
                /*width: 40%;*/
                padding: .15rem;
                border-radius: 10%;
                overflow: hidden;
                img {
                    width: 100%;
                    height: 100%;
                    border: 1px solid orange;
                    overflow: hidden;
                }
            }
        }
    }
    /*********动画*****/
    
    .bounce-enter,
    bounce-leave-to {
        opacity: 0;
    }
    
    .bounce-enter-active {
        animation: bounceIn 1.5s;
    }
    
    .bounce-leave-active {
        animation: bounceOut 1s;
    }
    
    @keyframes bounceIn {
        0% {
            opacity: 0;
            transform: translateX(50rem);
        }
        50% {
            transform: scale(.6);
        }
        100% {
            opacity: 1;
            transform: translateX(0);
        }
    }
    
    @keyframes bounceOut {
        0% {
            opacity: 1;
        }
        50% {
            transform: scale(.6);
        }
        100% {
            opacity: 0;
            transform: translateX(-50rem);
        }
    }
</style>


