<template>
<div class="footer">
        <div class="tab-item" >
            <router-link to="/goods" class="active">
                <span class="icon-home"></span>
                <span>主页</span>
            </router-link>
        </div>
        <div class="tab-item">
            <router-link to="/ratings">
                <span class="icon-food"></span>
                <span>精选</span>
            </router-link>
        </div>
        <div class="tab-item">
            <router-link to="/seller">
                <span class="icon-comments-alt"></span>
                <span>圈子</span>
            </router-link>
        </div>
        <div class="tab-item">
            <router-link to="/seller">
                <span class=" icon-user"></span>
                <span>坊主</span>
            </router-link>
        </div>
    <router-view></router-view>
</div>
</template>

<script>
    export default {
        data() {
            return {
                currentActive: 0
            }
        }
    }
</script>

<style lang="less" scoped>
    .footer {
        display: flex;
        z-index:100;
        height:2.2rem;
        padding-top: .2rem;
        background: #fff;
        .tab-item{
            flex: 1;
            text-align: center;
            a {
                color: #666;
                text-align: center;
                &.active {
                    border-top:1px solid #2bc57f;
                    span:nth-child(1) {
                        color: #2bc57f;
                    }
                    }
                    span {
                        display: block;
                        font-size: .8rem;
                    &:nth-child(1) {
                         font-size: 1.1rem;
                     }
                    &:nth-child(2) {
                         font-size: .7rem;
                     }
                }
            }
        }

    }
</style>


