<template>
    <div class="hotSell">
            <h3 class="title"><span class="icon-fire"></span>秒杀专区</h3>
            <div class="goods">
                <ul  @touchstart.stop="onTouchStart"
                @touchmove.stop="onTouchMove"
                    ref="hotScroll"
                     v-if="hotGoods">
                    <li v-for="item in hotGoods.goods" ref="goodsHook">
                        <router-link to="" class="goods_link">
                            <img :src="item.img">
                            <p><span>￥{{item.price}}</span><span>￥{{item.oldPrice}}</span></p>
                        </router-link>
                    </li>
                </ul>
            </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                dataAll: {},
                autoScroll: null,
                startX: null,
                scrollX: null
            }
        },
        mounted() {
            this.$nextTick(function() {
                let self = this;
                setTimeout(function() {
                    self.setWidth();
                }, 50)
            });
        },
        props: {
            hotGoods: {
                type: Object
            }
        },
        methods: {
            setWidth() {
                let oUl = this.$refs.hotScroll;
                let oLi = this.hotGoods.goods.length;
                oUl.style.width = oLi * 5 + "rem";
            },
            autoPlay() {
                let oUl = this.$refs.hotScroll;
                if (oUl.style) {
                    oUl.style.transform = "translateX(" + this.scrollX + "px)";
                }
            },
            onTouchStart(event) {
                let touch = event.changedTouches[0];
                this.startX = touch.clientX;
                let oUl = this.$refs.hotScroll;
                let currentTranX = oUl.style.transform;
                if (currentTranX) {
                    this.scrollX = -parseInt(currentTranX.replace(/[^0-9]/ig, ""));
                } else {
                    this.scrollX = 0;
                }
            },
            onTouchMove(event) {
                let touch = event.changedTouches[0];
                let moveX = touch.clientX;
                let distance = parseInt(this.startX - moveX);
                let oUl = this.$refs.hotScroll;
                if (distance >= 10) {
                    this.scrollX += -5;
                }
                if (distance <= -10) {
                    this.scrollX += 5;
                }
                let oLi = this.hotGoods.goods.length * 5;
                if (this.scrollX > 0) {
                    this.scrollX = 0;
                } else if (-this.scrollX > (oLi - 18.75)) {
                    this.scrollX = (-oLi + 18.75);
                }

                oUl.style.transform = "translateX(" + this.scrollX + "rem)";

                this.startX = moveX;
            }

        }

    }
</script>
<style lang="less" scoped>
    .hotSell {
        .title {
            line-height: 1.2rem;
            font-size: .8rem;
            color: lighten(red, 10%);
            text-align: center;
            span {
                margin-right: .2rem;
            }
        }
        .goods {
            overflow: hidden;
            ul {
                display: flex;
                width: 200%;
                height: 4rem;
                margin-top: .2rem;
                overflow: hidden;
                transition: .4s transform ease;
                li {
                    width: 5rem;
                    .goods_link {
                        display: block;
                        width: 100%;
                        height: 100%;
                        text-align: center;
                        img {
                            width: 70%;
                            height: 70%;
                            border-radius: 20% 20% 0 0;
                            overflow: hidden;
                        }
                        p {
                            height: .8rem;
                            margin-top: .2rem;
                            line-height: .8rem;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            span:nth-child(1) {
                                margin-right: .2rem;
                                color: lighten(red, 10%);
                                font-weight: bold;
                            }
                            span:nth-child(2) {
                                color: #666;
                                text-decoration: line-through;
                            }
                        }
                    }
                }
                &:nth-child(2) {
                    /*flex:1.5;*/
                    background-color: red;
                }
            }
        }
    }
</style>


