<template>
  <div class="banner">
    <ul v-if="bannerData">
        <transition-group name="fade">
            <li v-for="(item,index) in bannerData" v-if="currentShow==index" 
                @touchstart="onTouchStart"
                @touchend="onTouchEnd"
                :key="index"
                >
                    <router-link :to="{name:'detail',params:{dataId:item.dataId}}" class="bannerLink">
                        <img :src="item.img">
                        <p>{{item.text}}</p>
                    </router-link>
            </li>
        </transition-group>
    </ul>
    <div class="bannerPonit">
       <span 
        v-for="(item,index) in bannerData"
        v-bind:class="{active:index === currentShow}">
       </span>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                currentShow: 0,
                ponitActive: 0,
                startX: 0,
                autoScroll: null
            }
        },
        props:{
            bannerData:[Array]
        },
        watch: {
            "bannerData": function () {
                this.$nextTick(function(){
                    this.autoPlay();
                })
            }
        },
        methods: {
            autoPlay() {
                let self = this;
                let len = this.bannerData.length;
                this.autoScroll = setInterval(function() {
                    self.currentShow++;
                    if (self.currentShow > len - 1) {
                        self.currentShow = 0;
                    }
                }, 5000);
            },
            onTouchStart(event) {
                clearInterval(this.autoScroll);
                let touch = event.changedTouches[0];
                this.startX = touch.clientX;
            },
            onTouchEnd() {
                let touch = event.changedTouches[0];
                let endX = touch.clientX;
                let distance = Math.abs(this.startX - endX);
                let len = this.bannerData.length;
                //往右
                if (distance > 20) {
                    if (this.startX < endX) {
                        this.currentShow--;
                        if (this.currentShow < 0) {
                            this.currentShow = len - 1
                        }
                    } else {
                        this.currentShow++;
                        if (this.currentShow > len - 1) {
                            this.currentShow = 0;
                        }
                    }
                }
                this.autoPlay();
            }

        }

    }
</script>

<style scoped lang="less">
    .banner {
        position: relative;
        height: 10rem;
        overflow: hidden;
        ul {
            height: 10rem;
            overflow: hidden;
            li {
                height:100%;
                position: absolute;
                left: 0;
                top: 0;
                .bannerLink {
                    display: black;
                    width: 100%;
                    height: 100%;
                }
                p {
                    position: absolute;
                    bottom: 1.8rem;
                    right: 1.5rem;
                    color: #fff;
                }
                img {
                    width: 100%;
                }
            }
        }
      
        .bannerPonit {
            position: absolute;
            z-index: 10;
            bottom: .5rem;
            margin-left: -1.1rem;
            left: 50%;
            span {
                display: inline-block;
                width: 0.5rem;
                height: .5rem;
                margin-left: 0.25rem;
                background: #fff;
                border: 1px solid #ccc;
                border-radius: 50%;
            }
            .active {
                background: black;
            }
        }
    }
    /******动画*****/
    
    .fade-enter,
    fade-leave-to {
        opacity: 0;
    }
    .fade-enter {
        -webkit-transform:translateX(-100vw);
        transform: translateX(-100vw);
    }
    .fade-enter-active,
    .fade-leave-active {
        -webkit-transition: all 1s;
        transition: all 1s;
    }
</style>


