import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index.vue'
import home from '@/components/home/home.vue'
import recommendation from '@/components/recommendation/recommendation.vue'
import oMessage from '@/components/message/message.vue'
import uerCenter from '@/components/userCenter/userCenter.vue'
import login from "@/components/login/login.vue"
import register from "@/components/register/register.vue"
import detail from "@/components/detail/detail.vue"
import menuDetail from "@/components/detail/menuDetail.vue"


Vue.use(Router);

export default new Router({
    routes: [

        {
            path: '/home',
            component: home,
            children: [
                {
                    path: 'index',
                    component: index
                },
                {
                    path: 'recomendation',
                    component: recommendation
                },
                {
                    path: 'message',
                    component: oMessage
                },
                {
                    path: 'userCenter',
                    component: uerCenter
                }
            ]
        },
        {
            path: '/login',
            component: login
        },
        {
            path: '/register',
            component: register
        },
        {
            name:'detail',
            path: '/detail:dataId',
            component:detail
        },{
            name:'menuDetail',
            path: '/menuDetail:dataId',
            component:menuDetail
        }
    ]
})


