<template>
    <div class="detail">
        <div class="head">
            <h1 class="title">
                <span  @click="back" class="icon-chevron-left goBack"></span>
                <span class="text">{{dataAll.listTitle}}</span>
                <span class="icon-share"></span>
            </h1>
        </div>
        <div class="content">
            <div class="foodPic">
                <img :src="dataAll.listPic">
                <div class="foodJudge">
                    <span class="browse"><span class="icon-eye-open"></span>浏览{{dataAll.browseTime}}</span>
                    <span class="collection"><span class="icon-star"></span>收藏{{dataAll.collection}}</span>
                </div>
            </div>
            <div class="foodInfo">
                <h3 >{{dataAll.listTitle}}</h3>
                <p>{{dataAll.listText}}</p>
                <p class="ratings"><span >{{dataAll.score}}</span> {{dataAll.ratingsPeople}}人评分 <span v-if="dataAll.boutique" class="boutique">精</span></p>
                <v-star :score="dataAll.score" classs="star"></v-star>
                <div class="foodAuthor">
                    <div>
                        <router-link to="user">
                            <img :src="dataAll.listPic">
                        </router-link>
                    </div>
                    <div>{{dataAll.user}}</div>
                    <div @click="followState=!followState"><span class="icon-plus" v-if="!followState"></span><span v-text="!followState ? '关注':'已关注'"></span></div>
                </div>
                <div class="foodMake">
                    <div class="time">
                        <p><span class="icon-time"></span> 时间：{{dataAll.postTime}}</p>
                        <p><span class="icon-signal"></span> 难度：{{dataAll.hardLevel}}</p>
                    </div>
                    <p><span class="icon-ok-sign"></span> {{dataAll.descript}}</p>
                    <div class="stuffListTitle">
                        <div class="line"></div>
                        <div class="title">食材清单</div>
                        <div class="line"></div>
                    </div>
                    <div class="stuffList">
                        <ul>
                            <li v-for="item in dataAll.stuffList" class="stuffList_li">
                                <p>{{item.name}}</p>
                                <p>{{item.number}}</p>
                            </li>
                        </ul>
                    </div>
                    <div class="stuffListTitle">
                        <div class="line"></div>
                        <div class="title">美食步骤</div>
                        <div class="line"></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="detailMenu">
                <p>加入采购单</p>
                <p><span class="icon-comment-alt"></span> 评论</p>
                <p><span class="icon-star-empty" ></span> 收藏</p>
        </div>
    </div>
</template>

<script>
    import oStar from "../star/star.vue";
    export default {
        name: 'detail',
        data(){
            return {
                followState: false,
                dataAll: {}
            }
        },
        components: {
            "v-star": oStar
        },
        beforeRouteEnter(to, from, next){
            let dataId = to.params.dataId;
            next(function(vm){
                vm.$http.get('./api/index').then(function(data){
                    let dataList = data.data.data.indexListData;
                    for(var i=0;i<dataList.length;i++){
                        if(dataId == dataList[i].dataId){
                            vm.dataAll = dataList[i];
                        }
                    }
                })
            })
        },
        methods: {
            back(){
                this.$router.go(-1)
            }
        }
    }
</script>

<style scoped lang="less">
    .detail{
        overflow: hidden;
    }
    .head {
        padding-top: 0.5rem;
        background: #2bc57c;
    }
    .title {
        display: flex;
        height: 1.8rem;
        justify-content:space-around;
        align-items:center;
        color: white;
        font-size: .8rem;
        line-height: 1.8rem;
        font-weight: 100;
    span{
        text-align:center;
    &:nth-child(odd){
         flex: 1;
         font-size:.8rem;

     }
    &:nth-child(2){
         flex:4;
         text-align: center;
     }
    }
    }
    .content{
        .foodPic{
            position: relative;
            width: 100%;
            height: 10rem;
            over-flow:hidden;
            img{
                width: 100%;
                height: 100%;
            }
            .foodJudge{
                position: absolute;
                z-index: 5;
                right: .5rem;
                bottom: .2rem;
                color:#666;
                .collection span{
                    color: lighten(red,5%);
                }
            }
        }
        .foodInfo{
            padding: 1rem;
            text-align: center;
            h3{
                font-size: .8rem;

            }
            .star{
                width: 6.75rem;
                padding: 0 5rem;
            }
            .ratings{
                line-height: 1.5rem;
                color: #666;
                /*border: 1px solid red;*/
                span{
                    margin-right: .3rem;
                    color: black;
                    font-size: .8rem;
                }
                .boutique{
                    margin-left: .4rem;
                    padding: 0 .4rem;
                    color: #fff;
                    background-color: lighten(red,10%);
                    font-size: .5rem;
                    border-radius: .4rem;
                }
            }
            .foodAuthor{
                display: flex;
                height:1.5rem;
                line-height:1.5rem;

                div:nth-of-type(1){
                     width:1.5rem;
                    height: 1.5rem;
                    border-radius:50%;
                    overflow:hidden;
                    font-size:0;
                        img{
                            width: 100%;
                            height: 100%;
                        }
                }
                div:nth-of-type(2){
                     flex:1;
                    padding-left: .5rem;
                    line-height: 1.5rem;
                    text-align: left;
                 }
                div:nth-of-type(3){
                    width:2.5rem;
                    line-height: 1.5rem;
                    border-radius: 1.5rem;
                    border: 1px solid lighten(red,10%);
                    span:nth-of-type(1){
                            margin-right:.1rem;
                            color: lighten(red,10%);
                        }
                    span:nth-of-type(2){
                            color: #666;
                        }
                 }
            }
    .foodMake{

        .time{
            display: flex;
            line-height: 1.5rem;
            color:#666;
            p{
                flex: 1;
                text-align: left;
            }
        }
        p{
            color:#666;text-align: left;
        }
        .stuffList{
            margin:0 0 .6rem 0;
        }
        .stuffListTitle{
            display: flex;
            justify-content:center;
            .title{
                width: 4rem;
                padding: 0 .2rem;
                line-height: 1.5rem;
                color: black;
                text-align: center;
            }
            .line{
                width: 1.5rem;
                margin-top: .85rem;
                border-top: 1px solid rgba(100,100,100,.6);
                -webkit-transform: scaleY(.5);
                -webkit-transform-origin: 0 0;
            }
        }
        .stuffList_li{
            display: flex;
            line-height: 2rem;
        border-bottom:1px solid rgba(100,100,100,.2);
            p{
                flex: 1;
                font-size: .7rem;
            }
            &:nth-of-type(even){
                :nth-child(1){
                    color: #2bc57f;
                }
            }
        }
    }

        }

    }
    .detailMenu{
        position: fixed;
        bottom: 1rem;
        right: .35rem;
        display: flex;
        width: 60%;
        height: 1.5rem;
        line-height: 1.5rem;
        justify-content: center;
        border:1px solid orange;
        border-radius: 1.5rem 0 0 1.5rem;
        background-color:#fff;

        p{
            flex: 1;
            text-align: center;
            color: #666;
        }
    }

</style>


