<template>
    <div class="form">
        <ul class="userInfo">
            <li class="form_li">
                <div class="counter_text">
                    <span class="icon-user" ></span>
                    <span>账号</span>
                </div>
                <div class="couter">
                    <input type="text" placeholder="请输入手机号码" class="phoneNumber" v-model="userCount">

                </div>
            </li>
            <li class="form_li">
                <div class="counter_text">
                    <span class="icon-wrench" ></span>
                    <span>密码</span>
                </div>
                <div class="couter">
                    <input type="password" placeholder="请输入密码" class="passWord" v-model="userPassWard">
                </div>
            </li>
        </ul>
        <ul class="submit_Ul">
            <li class="forgetPossward">
                <router-link to="/">忘记密码?</router-link>
            </li>
            <li class="submit">
                <input type="button" value="登录" @click="login">
            </li>
            <li class="question">
                <router-link to="/">登录遇到问题</router-link>
            </li>
        </ul>
        <div class="loginOk" v-if="loginOk">
            <p>登录成功，马上转到美坊首页！</p>
        </div>
    </div>
</template>

<script>
    import oTools from "../../public/js/tools"
    export default {
        data() {
            return {
                userCount:null,
                userPassWard:null,
                loginOk:false
            }
        },
        methods:{
            login(){
                let username=this.userCount,passward=this.userPassWard;
//                ***********查找本地用户*****
                let userState=oTools.locationGetUser(username,"locationUser");
                if(userState){
                     if(passward === userState.passWard){
                         this.loginOk=true;
                         let self=this;
                         oTools.locationPushUser(userState,"currentUser");
                         console.log(localStorage);
                         setTimeout(function(){
                             self.$router.replace("/home/index");
                         },3000);
                     }else{
                         alert("密码错误，重新输入");
                     }
                }else{
                    alert("请注册");
                }
            }
        }
    }
</script>
<style lang="less" scoped>
    @import "../../public/less/base.less";
    .form {
        .userInfo {
            padding: 0 1rem;
            li {
                display: flex;
                padding-top: .5rem;
                line-height: 2rem;
                .border1px(1px, rgba(100, 100, 100, .5));
                .counter_text {
                    width: 4rem;
                    font-size: .75rem;
                    text-align: center;
                    span {
                        width: 2rem;
                        &:nth-child(1) {
                            margin-right: .2rem;
                        }
                    }
                }
                .couter {
                    flex: 1;
                    input {
                        line-height: 1.5rem;
                        border: none;
                        padding: .2rem.5rem;
                        font-size: .75rem;
                        &::-webkit-input-placeholder,
                        textarea::-webkit-input-placeholder {
                            color: rgba(50, 50, 50, .3);
                        }
                        &:-ms-input-placeholder,
                        textarea:-ms-input-placeholder {
                            color: rgba(50, 50, 50, .3);
                        }
                        &:-moz-placeholder,
                        textarea:-moz-placeholder {
                            color: rgba(50, 50, 50, .3);
                        }
                    }
                }
            }
        }
        .submit_Ul {
            li {
                height: 2rem;
                line-height: 2rem;
                /*border: 1px solid red;*/
            }
            .forgetPossward {
                a {
                    display: block;
                    width: 4rem;
                    height: 2rem;
                    padding-right: 1rem;
                    line-height: 2rem;
                    float: right;
                    color: rgba(50, 50, 50, .3);
                    font-size: .75rem;
                }
            }
            .submit {
                padding: .8rem 1.7rem 0;
                input {
                    width: 100%;
                    height: 2rem;
                    border: none;
                    color: white;
                    background-color: #2bc57c;
                    font-size: .8rem;
                    border-radius: 2rem;
                    box-shadow: none;
                }
            }
            .question {
                padding-top: .5rem;
                a {
                    display: block;
                    width: 100%;
                    height: 100%;
                    text-align: center;
                    color: #2bc57f;
                    font-size: .7rem;
                }
            }
        }
    }
    .loginOk{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        line-height: 100vh;
        text-align: center;
        color: #2bc57f;
        font-size: 1rem;
        font-weight: 100;
        background-color: rgba(100,100,100,.8);
    }
</style>


