<template>
  <div class="header">
      <h1 class="title">
          <span class="icon-reorder menuHeader" @click="userCenterState = !userCenterState"></span>
          <input type="text" placeholder="搜美食/商品/达人" class="search">
          <span class="icon-bell message"></span>
      </h1>
      <transition-group name="fade">
          <div class="indexUserCenter"  v-if="userCenterState" key="1">
              <div class="contentBox">
              <v-userCenter class="userCenterContent"></v-userCenter>
                </div>
              <div class="close" @click="userCenterState = !userCenterState">
                  <span class="icon-remove"></span>
              </div>
          </div>
      </transition-group>

  </div>

</template>

<script>
    import userCenter from "../userCenter/userCenter.vue"

    export default {
        data(){
            return {
                userCenterState:false
            }
        },
        components:{
            "v-userCenter":userCenter
        }
    }
</script>

<style scoped lang="less">
    .header {
        padding-top: 0.5rem;
        background: #2bc57c;
    }
    
    .title {
        /*font-family: "";*/
        padding-bottom: 0.25rem;
        text-align: center;
        color: white;
        font-size: 1rem;
        line-height: 1.8rem;
        span {
            position: absolute;
            z-index: 10;
            top: 1rem;
        }
        .menuHeader {
            left: .5rem;
        }
        .message {
            right: .5rem;
        }
        .search {
            padding: .15rem 0 .15rem .5rem;
            border-radius: 0.5rem;
            border: none;
        }
    }
    /************/
    .indexUserCenter{
        position: fixed;
        z-index: 999;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color:rgba(20,20,20,.8);
        perspective:300;
        perspective-origin: 30% 30%;
        -webkit-perspective:300;
        -webkit-perspective-origin: 30% 30%;
        transform-origin: left top;
        .contentBox{
            height: 94vh;
            padding-bottom: 2rem;
            overflow: scroll;
        }
        .close{
            position: relative;
            z-index: 10;
            height: 2rem;
            margin-top: -2rem;
            text-align: center;
            color: white;
            line-height: 2rem;
            font-size: 1rem;
            font-weight: 100;
            background-color: rgba(50,50,50,.7);
            span{
                line-height: 2rem;
                font-size: 1.4rem;
                font-weight: 100;
            }
        }

    }
    /******动画*****/

    .fade-enter,
    .fade-leave-to{
        transform: translateX(-100vw);
        -webkit-transform: translateX(-100vw);
    }
    .fade-enter-active,.fade-leave-active{
        transition: all .5s;
        -webkit-transition: all .5s;
    }

</style>


