<template>
    <div class="msgListItem">
        <div class="list_title">
            <div class="line"></div>
            <div class="text">热门精贴</div>
            <div class="line"></div>
        </div>
        <ul >
              <li v-for="(item,index) in listData" class="foodslist">
                 <router-link :to="item.dataId">
                     <div class="foodsImg">
                         <img :src="item.img">
                     </div>
                     <div class="foodsInfo">
                             <div class="foodDescript">
                                 <h2>{{item.title}}</h2>
                                 <div class="foodStar">
                                     <v-star :score="4.5" class="star"></v-star>
                                     <div>
                                         <span class="starScore">4.5</span>
                                         <span class="icon-eye-open"></span><span>{{item.browse}}</span>
                                         <span class="icon-comment-alt"></span><span>{{item.comment}}</span>
                                     </div>
                                 </div>
                             </div>
                         <div class="user">
                             <div class="userPic">
                                 <img :src="item.img">
                             </div>
                             <div class="userName">
                                 <span>{{item.userName}}</span>
                                 <span>{{item.postTime}}分钟前</span>
                             </div>
                         </div>
                     </div>


                 </router-link>

              </li>
        </ul>


    </div>
</template>
<script>
    import star from "../../star/star.vue"
    export default{
        data(){
            return{
            }
        },
        props:{
            listData:[Object,Array]
        },
        components:{
            "v-star":star
        }
    }
</script>

<style lang="less" scoped>
    @import "../../../public/less/base.less";
    .msgListItem{
        ul{
            transition: all 1s;
            li{
                position: relative;
            }
        }
    }

    .list_title {
            display: flex;
            padding: .5rem 1rem 0;
            .line {
                flex: 1;
                position: relative;
                top: -.86rem;
                .border1px(1px,#2bc57f)
            }
            .text {
                padding: .2rem .8rem;
                font-size: .8rem;
                border:1px solid #2bc57f;
                border-radius: 50%;
            }
        }
    .foodslist{
            height:5.5rem;
            padding: .5rem 1rem;
            box-sizing: border-box;
            overflow:hidden;
    .border1px (1px, #ccc);
            a{
                display: flex;
                flex-flow:row;
            }
            .foodsInfo{
                flex: 1;
                padding-left: 1rem;
                color:black;
                .user{
                    display: flex;
                    height:2rem;
                    .userPic{
                        width: 2rem;
                        margin-right:.5rem;
                        border-radius: 50%;
                        overflow: hidden;

                        img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .userName{
                        flex: 1;
                        display: flex;
                        span:nth-child(1){
                            flex: 1;
                            line-height:2rem;
                            font-size: .8rem;
                            color: black;
                        }
                        span:nth-child(2){
                            width: 3rem;
                            margin-top: .15rem;
                            line-height:2rem;
                            color: #666;
                        }
                    }
                }
                .foodDescript{
                    h2{
                        height: 1.5rem;
                        line-height: 1.5rem;
                    }
                    .foodStar{
                        display: flex;
                        margin:-.1rem 0 .1rem 0;
                        .star{
                            width: 5rem;

                            margin-right: .5rem;
                        }
                        div{
                            line-height: 1rem;
                        }
                        .starScore{
                            color: orange;
                            margin-left: -.3rem;
                            margin-right: .3rem;
                            line-height: 100%;
                            font-size: .8rem;
                        }
                    }
                }

            }
            .foodsImg{
                width:4.5rem;
                img{
                    width: 100%;
                }
            }
    }



</style>


