<template>
    <div class="scrollNews">
        <div class="logo">
                <p>美坊—</p>
                <p>—热文 </p>
        </div>
        <div class="news">
            <ul>
                <transition name="slide-fade" v-for="(item,index) in newsData" key="index">
                    <li v-show="( index == currentItem ) || (index - 1 == currentItem)">
                        <p>
                            <span :class='changIcon(item.iconNumber)' ></span>
                            {{item.text}}
                        </p>
                    </li>
                </transition>
            </ul>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                currentItem: 0,
                autoTimer: null
            }
        },
        props: {
            newsData: {
                type: Array
            }
        },
        mounted() {
            this.autoTimer = setInterval(() => {
                this.autoPlay()
            }, 2000)
        },
        methods: {
            changIcon(n) {
                let idx = parseInt(n)
                let className = "icon-coffee";
                switch (idx) {
                    case 0:
                        className = "icon-coffee";
                        return className;
                        break;
                    case 1:
                        className = "icon-bullhorn";
                        return className;
                        break;
                    case 2:
                        className = "icon-thumbs-up";
                        return className;
                        break;
                    case 3:
                        className = "icon-leaf";
                        return className;
                        break;
                    default :
                        return className;
                }
            },
            autoPlay() {
                let newsLength = this.newsData.length;
                if (this.currentItem >= newsLength - 2) {
                    this.currentItem = 0;
                } else {
                    this.currentItem += 2;
                }

            }

        }
    }
</script>
<style lang="less" scoped>
    .scrollNews {
        display: flex;
        height: 2rem;
        overflow: hidden;
        .logo {
            flex: 2;
            p {
                font-family: arial, Arial, Helvetica, sans-serif;
                text-align: center;
                font-size: .8rem;
                color: red;
                transform: rotate(-7deg);
                &:nth-child(1) {
                    margin-bottom: -.3rem;
                }
                &:nth-child(2) {
                    margin-top: -.3rem;
                }
            }
        }
        .news {
            flex: 8;
            height: 100%;
            ul {
                height: 100%;
                overflow: hidden;
                li {
                    height: 50%;
                    p {
                        width: 14rem;
                        height: 1rem;
                        line-height: 1rem;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        span:nth-child(1) {
                            font-size: .8rem;
                            color: lighten(red, 15%);
                        }
                    }
                }
            }
        }
    }
    
    .slide-fade-enter-active {
        transition: all .3s ease;
    }
    
    .slide-fade-leave-active {
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    
    .slide-fade-enter,
    .slide-fade-leave-active {
        transform: translateX(10px);
        opacity: 0;
    }
</style>


