<template>
    <div class="indexList">
        <p class="listName">
            <span class="icon-fire"></span>最热
            <router-link to="" class="moreHotList">更多 <span class=" icon-hand-right"></span></router-link>
            </p>
        <ul>
            <li v-for="(item,index) in indexListData" class="list_Li">
                <router-link :to="{name:'detail',params:{dataId:item.dataId}}" class="list_link">
                    <div class="itemPic">
                          <img :src="item.listPic" :data-src="item.listPic">
                            <div class="itemInfo">
                                <span class=" icon-eye-open"> </span><span>{{item.browseTime}}</span>
                                <span class="icon-heart-empty"></span><span>{{item.collection}}</span>
                            </div>
                    </div>
                    <div class="itemTitle">
                        <h3 >{{item.listTitle}} </h3>
                        <div class="userInfo">
                            <span class="userheadPhoto">  <img :src="item.listPic"></span>
                            <span class="userName">{{item.user}}</span>
                        </div>
                    </div>
                    <p class="itemText">{{item.listText}}</p>
                </router-link>
            </li>
        </ul>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                defalutPic: "/static/img/indexList/indexList01.png"
            }
        },
        props:{
            indexListData:{
                type:Array
            }
        }
    }
</script>

<style lang="less" scoped>
    .listName {
        position: relative;
        margin: .1rem 0 0 0;
        padding-left: 1rem;
        span {
            color: red;
            font-size: 1rem;
        }
        .moreHotList {
            position: absolute;
            right: 1rem;
            top: 0;
            color: black;
        }
    }
    
    .indexList {
        .list_Li {
            overflow: hidden;
            /*-webkit-border-radius: 30% 30% 0 0;*/
            /*-moz-border-radius: 30% 30% 0 0;*/
            /*-o-border-radius: 30% 30% 0 0;*/
            /*border-radius: 30% 30% 0 0;*/
        }
        .list_link {
            display: block;
            color: black;
            text-align: center;
        }
        .itemPic {
            position: relative;
            width: 100%;
            height: 10rem;
            img {
                width: 100%;
                height: 100%;
            }
            .itemInfo{
                position: absolute;
                z-index: 10;
                right: 1rem;
                bottom:.5rem;
                span{
                    margin: 0 .05rem;
                    color: #fff;
                    font-size: 10px;
                }
            }
        }
        .itemTitle{
                position:relative;

                h3{
                    padding-right: 7rem;
                    text-align:center;
                    line-height: 2rem;
                }
                .userInfo{
                    display: flex;
                    position: absolute;
                    right: 0;
                    top:0;
                    width: 6rem;
                    height: 2rem;
                    padding-right: .5rem;
                    line-height: 2rem;
                    .userheadPhoto{
                        flex: 0 0 1;
                        width: 1.6rem;
                        height: 1.6rem;
                        margin: .2rem .5rem ;
                        border-radius: 50%;
                        overflow: hidden;
                        img{
                            width: 100%;
                            height: 100%;
                            margin-top: -.6rem;
                        }
                    }
                    .userName{
                        color: #333;
                    }

                }
        }
        .itemText {
            height: 2rem;
            margin-bottom: .5rem;
            padding: 0 .5rem;
            line-height: 1rem;
            color: #666;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }
    }
</style>


